body {
  font-family: 'Open Sans', sans-serif;
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}
.preload {
  height: 100vh;
  width: 100vw;
  background-color: black;
}
.preload svg {
  margin: auto;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
}
.preload svg path {
  fill: none;
  stroke: #fff;
  stroke-width: 4;
}
.preload svg .st0 {
  opacity: 0;
}
#Polystar_1 {
  opacity: 0;
}
@media (max-width: 800px) {
  .preload svg {
    transform: scale(2);
  }
}
